.heartbackground {
    margin-top: 3rem;
    width: 100vw;
    height: 10rem;
    // border: 1px solid black;
    background-color: #49C999;

    ul {
        display: flex;

        .hearttitle {
            color: white;
            font-size: 2rem;
            margin-top: 2rem;
        }
        .heartimg {
            img {
                width: 8rem;
                height: 8rem;
                margin-left: 0.3rem;
            }
        }
    }
}
.heartrank{
    margin-top: 1rem;
   .rankitem{
       width: 100vw;
       height: 2rem;
       border-bottom: 1px solid#CBCBCB;
       ul{
           position: relative;
           list-style: none;
           display: flex;
           padding: 0;
           margin: 0;
           width: 100%;
           height: 100%;
           .itemhead{
               font-family:"STKaiti" ;
               font-size: 1.5rem;
               color:#49C999 ;
               margin-left: 1rem;
           }
           img{
               width: 1.7rem;
               height: 1.7rem;
               margin-left: 2rem;
           }
           .itemperson{
             margin-top: 0.4rem;
             margin-left: 0.2rem;
           }
           .itemtime{
             
               position: absolute;
               right: 1rem;
               margin-top: 0.4rem;
           }
       }
   } 
}