.pcheader{
    width: 100vw;
    height: 10vh;
    // border: 1px solid black;
    background-color: #1448BB;
    line-height: 10vh;
    display: flex;
    position: relative;
    .pctitle{
        color: white;
        font-size: 20px;
        margin-left: 1rem;
    }
    .pcadmin{
        color: white;
        position: absolute;
        right: 5rem;
        cursor: pointer;
    }
    .pcback{
        color: white;
        position: absolute;
        right: 1.5rem;
        cursor: pointer;
    }
}
.pccontent{
    width: 100vw;
    height: 85vh;
    // border: 1px solid black;
    display: flex;
    .pcslider{
        width: 15vw;
        height: 100%;
        // border: 1px solid black;
        margin-left: 2vw;
    }
    .pcdisplay{
        margin-left: 2vw;
        width: 80vw;
        height: 100%;
        // border: 1px solid black;
        .pcdisplay-title{
            width: 100%;
            height: 3rem;
            border-bottom: 0.15rem solid #77BAED;
            font-weight:bolder;
            padding-top: 0.5rem;
            font-size: 14px;
            
        }
        .pcdisplay-search{
            width: 100%;
            height: 2rem;
            // border: 1px solid black;
            padding-left: 2rem;
            
        }
    }
}
.pcfoot{
    width: 100vw;
    height: 5vh;
    // border: 1px solid black;
    background-color: #ECE9EC;
    line-height: 5vh;
    text-align: center;
}