.recevelist{
    margin-top: 3.5rem;
    width: 100vw;
    height: 20rem;
    // border: 1px solid black;
    .receive-head
    {
        width: 100vw;
        height: 2.4rem;
        border-bottom: 1px solid #CBCBCB;
        display: flex;
        line-height: 2.4rem;
        align-items: center;
        position: relative;
        img{
            display: block;
            width: 2rem;
            height: 2rem;
            border-radius: 50%;
            border: 1px solid transparent;
            margin-left: 0.5rem;
        }
        div{
            font-size: 1rem;
            font-weight: bolder;
            margin-left: 1rem;
        }
        button{
          position: absolute;
          right: 0;
          height: 100%;
          width: 5rem;
          background-color:#68E9B9 ;
          color: white;
          outline: none;
          border: 0;
        }
    }
    .receive-introduce{
      width: 100vw;
      height: 7rem;
    //   border: 1px solid black;
    //   border-bottom: 1px solid #CBCBCB;
      ul{
          .receive-title{
              display: flex;
              
              span{
                  color: #FF7070;
                  border: 1px solid #FF7070;
                  width: 3rem;
                  height: 1rem;
                  border-radius: 5px;
                  text-align: center;
                  line-height: 1rem;
                  font-size: 12px;
                  display: block;
                  background-color:white;
                  margin-bottom: 0.5rem;
              }
          }
          .receive-body{
              color:#CBCBCB;
              text-indent: 1.5rem;
          }
      }
      .geli{
          width: 100vw;
          height: 0.5rem;
        //   border: 1px solid rgb(88, 88, 88);
          background-color:#F5FFFC;
      }
    }
}