.head{
    width: 100vw;
    height: 16rem;
    //  border: 1px solid black;
    box-sizing: border-box;
    background-image: url('../image/background.png');
    background-size: 100%;
    position: relative;
    .title{
        position: absolute;
        left: 50%;
        font-family: "华文新魏";
        font-size: 2rem;
        transform: translateX(-1.8rem);
        color: white;
         top: 3rem;
    }
    img{
        display: block;
        width: 8rem;
        height: 8rem;
        border-radius: 50%;
        position: absolute;
        //top: 40%;
         left: 50%;
         transform: translateX(-4rem);
         top: 6rem;
        // margin-top: 5rem;
    }
}
.am-list-item{
    height: 60px !important;
   
}
.am-button-primary {
    background-color: #49C999 !important;
}
.foot{
    font-size: 16px;
    margin-top: 2rem;
    width: 90vw;
    height: 5rem;
    // border: 1px solid black;
    ul{
        display: flex;
        justify-content: space-between;
    }
}