.introduceback{
    width: 100vw;
    height: 35rem;
    // border: 1px solid black;
    background-color:#68E9B9 ;
    .introducetitle{
      width: 100vw;
      height: 3.6rem;
    //   border: 1px solid black;
      color: white;
      font-size: 1rem;
      padding-left: 0.5rem;
      padding-top: 0.5rem;
      .subtitle{
        // border: 1px solid black;
        width: 3.6rem;
        font-size: 0.8rem;
        border-radius: 5px;
        text-align: center;
        line-height: auto;
        background-color: #FFFB0C;
        color:#68E9B9  ;
        margin-top: 0.5rem;
      }
    }
    .introduce-item{
        width: 100vw;
        height: 4rem;
        // border: 1px solid black;
        display: flex;
        align-items: center;
        .introduceicon{
          display: block;
          width: 2rem;
          height: 2rem;
          margin-left: 0.5rem;
        }
        ul{
            list-style: none;
            color: white;
            // border: 1px solid black;
            padding: 0;
            height: 2.5rem;
            margin-left: 1rem;
            .organizetion{
                font-size: 0.7rem;
                color:#FFFB0C ;
                margin-bottom: 0.5rem;
            }
        }
    }
    .introducecenter{
        width: 100vw;
        height: 15rem;
        // border: 1px solid black;
        text-align: center;
        background-color: white;
    }
}
.sign-up{
    width: 100vw;
    height: 3rem;
    border-top: 1px solid #F2F2F2;
    //border: 1px solid black;
    //position: absolute;
    margin-top: 0.5rem;
    display: flex;
    align-items: center;
    img{
        width: 2rem;
        height: 2rem;
        display: block;
        margin-left: 0.5rem;
    }
    .sign-button{
        width: 19rem !important;
        border-radius: 3rem;
        margin-left: 1rem;
        height: 2.5rem;
    }
}