.contain{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    h1{
        margin: 3rem auto;
        text-align: center;
        color: #68E9B9;
    }
    .pclogin-form{
        width: 35rem;
        height: 15rem;
        border-top: 1px solid #E5E5E5;
        border-bottom: 1px solid #E5E5E5;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        .pclogin-login{
            width: 15rem;
            height: 13rem;
            // border: 1px solid black;
        }
        .pclogin-image{
            width: 15rem;
            height: 13rem;
            // border: 1px solid black;
            .background{
                width: 12rem;
                height: 10rem;
                background-color: #FAFAFA;
                margin: 0.5rem auto;
                text-align:center;
                img{
                    width: 8rem;
                    height: 8rem;
                    margin: 0 auto;
                    display: block;
                }
                h6{
                    font-size: larger;
                }
            }
        }
    }
}