.personcenter {
    .persontitle {
        width: 100vw;
        height: 14rem;
        // border: 1px solid black;
        background-color: #68E9B9;
        position: relative;
        box-sizing: border-box;
        .personcircle {
            display: block;
            margin: 0  auto;
            width: 8rem;
            height: 8rem;
        }
        .personimg{
            display: block;
            width: 6rem;
            height: 6rem;
            border-radius: 50%;
            position: absolute;
            top: 1rem;
            left: 50%;
            margin-left: -3rem;
        }
        text-align: center;
        color: white;
        font-size: 20px;
    }
}