.header{
    width: 100vw;
    height: 3rem;
    // border: 1px solid black;
    background-color: #68E9B9;
    position: relative;
    position: fixed;
    top: 0;
    z-index: 100;
    .logo{
        width: 2rem;
        height: 2rem;
        display: block;
        position: absolute;
        top: 50%;
        transform: translateY(-1rem);
        left: 0.3rem;
    }
    .saoyisao{
        width: 2rem;
        height: 1.5rem;
        display: block;
        position: absolute;
        right:  0.5rem;
        top: 50%; 
        transform: translateY(-0.8rem);
    }
    .searched{
        width: 75%;
        height: 2rem;
        // border: 1px solid black;
        position: absolute;
        top: 50%;
        transform: translateY(-1rem);
        left: 11%;
        // transform: translateX(-9rem);
        background-color: #06CB9E;
        border-radius: 5px;
        display: flex;
        text-align: center;
        line-height: 2rem;
        color: white;
        .search{
            display: block;
            width: 2rem;
            height: 2rem;
            margin-left: 1rem;
        }
    }
}
.movement{
    width: 100vw;
    .chosen{
        display: flex;
        img{
            width: 1.5rem;
            height: 1.5rem;
            margin-left: 0.5rem;
        }
        .text{
            font-size: 18px;
            margin-left: 1rem;
            font-weight: bolder;
        }
    }
    margin-bottom: 4rem;
}
.bottom{
    width: 100vw;
    border-top: 1px solid #E6E6E6;
    position: fixed;
    bottom: 0;
    background-color: #F8F8F8;
    z-index: 100;
    //border: 1px solid black;
}
